import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);
var state = {
    imgCodeInfo: {}
};
var getters = {
    imgCodeInfo() {
        return state.imgCodeInfo
    }
};
var mutations = {
    setImgCodeInfo(state, obj) {
        state.imgCodeInfo = obj
    }
};
var actions = {
    changeImgCodeInfo(context, obj) {
        context.commit("setImgCodeInfo", obj);
    }
};
var store = new Vuex.Store({ state, getters, mutations, actions });

export default store;
