import { Dialog, Toast } from "vant";

export default {
  loading(msg) {
    Toast.loading({
      mask: true,
      message: msg || "加载中",
      duration: 0
    });
  },
  hideLoading() {
    Toast.clear();
  },
  alert(msg, tit) {
    Dialog.alert({
      title: tit || "提示",
      message: msg || "出错了"
    });
  },
  confirm(msg, tit, fun) {
    Dialog.confirm({
      title: tit || "提示",
      message: msg
    }).then(() => {
      //on confirm
      if (fun) {
        fun('confirm');
      }
    }).catch(()=>{
      if (fun) {
        fun('cancel');
      }
    });
  },
  success(msg) {
    Toast({
      type: "success",
      message: msg || "成功",
      mask: true,
      duration: 1000
    });
  },
  fail(msg, time) {
    Toast({
      type: "fail",
      message: msg || "失败",
      mask: true,
      duration: time || 1000
    });
  }
};
